.productcard {
    /* background-color: red; */
    @media (max-width: 576px) {
        max-width:225px;
        max-height:350px;
    }
    @media (min-width: 577px) {
        width:400px;
        height:578px;
    }
  }
.productcardImage {

    @media (max-width: 576px) {
        width:140px;
        height:200px;
    }
    @media (min-width: 577px) {
        width:240px;
        height: 360px;
    }
  }
.productTitle {
    margin: 0;
    text-align: center;
    @media (max-width: 576px) {
        font-size:20px
    }
    @media (min-width: 577px) {
        font-size:30px
    }
  }
.productSubTitle {
    margin: 0;
    text-align: center;
    @media (max-width: 576px) {
        font-size:16px;
        font-weight:200
    }
    @media (min-width: 577px) {
        font-size:25px;
        font-weight:200
    }
  }
.productPrice {
    margin: 0;
    text-align: center;
    @media (max-width: 576px) {
        font-size:18px;
        line-height:20px;
        font-weight:400;
    }
    @media (min-width: 577px) {
        font-size:30px;
        line-height:20px;
        font-weight:400;
    }
  }
